import React from 'react';
import './wired.css';

export default class Wired extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            form: "Survey",
            butt: "Submit",
            choice: props.op1,
            display: "none",
            editing: false
        }
        this.handleClick = this.handleClick.bind(this);
        this.handleChoice = this.handleChoice.bind(this);
        this.handleOther = this.handleOther.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    };

    handleClick() {
        this.setState({
            form: this.state.form === "Survey"? "Clicked": "Survey" 
        });
    }

    handleChoice(event) {
        
        if(event.target.value === "Other") {
            this.setState({
                display: "block",
                choice: event.target.value
            })
        } else {
            this.setState({
                display: "none",
                choice: event.target.value
            })
        }

    }

    handleOther(event) {
        if(this.state.choice === "Other") {
            this.setState({
                choice: event.target.value
            })
        }
    }

    handleSubmit(event) {
        alert('Your favorite is: ' + this.state.choice);
        event.preventDefault();
    }

    blurEvent = (e) => {
        this.setState({choice: e.target.value});
    };

    edit = () => {
        this.setState({editing: true});
    };

    componentDidMount() {
        
    }
    
    render() {
        
        return <div>
           
            <h2>Question #{this.props.title}</h2>
            <form onSubmit={this.handleSubmit}
            onClick={this.editing}
            style={{textAlign: "center"}}
            textAlign="center">
        <label>
    
          <h4>Favorite kind of {this.props.formItem}:</h4>
          <br />
          <select onChange={this.handleChoice} required>
          <option value="" disabled selected>Select your option</option>
            <option value={this.props.op1}>{this.props.op1}</option>
            <option value={this.props.op2}>{this.props.op2}</option>
            <option value={this.props.op3}>{this.props.op3}</option>
            <option value={this.props.op4}>{this.props.op4}</option>
            <option value={this.props.op5}>{this.props.op5}</option>
            <option value={this.props.op6}>{this.props.op6}</option>
            <option value={this.props.op7}>{this.props.op7}</option>
          </select>
          <input onChange={this.handleOther}
          onBlur={this.blurEvent}
          type="text" className="other" id="other" 
          style={{textAlign: "center",
           display: `${this.state.display}` }}/>
          
        </label>
        <br />
        
        <input className="subby" type="submit" value="Submit" />
      </form>
        </div>;
    }
}